import React from 'react'
import Grid from '@material-ui/core/Grid'
import { Fade } from 'react-awesome-reveal'

const labelStyle = {
  color: 'white',
  fontFamily: 'NotoSans',
  fontSize: '0.75em',
  textAlign: 'center',
  marginTop: '-0.25em',
}

const ChartBottomAxis = ({ data }) => (
  <Grid container direction="row" justify="space-around">
    {
        data[0].data.map((value, index) => (
          <Fade key={index} direction="top" delay={index * 150}>
            <Grid item key={value.x}>
              <Grid container direction="column">
                <Grid item>
                  <value.icon alt="weather icon" fill="white" />
                </Grid>
                <Grid item style={labelStyle}>
                  {value.x}
                </Grid>
              </Grid>
            </Grid>
          </Fade>
        ))
      }
  </Grid>
)

export default ChartBottomAxis
