/* eslint-disable react/jsx-filename-extension */

import React, { useState } from 'react'
import { BrowserRouter as Router, Redirect, Route, Switch } from 'react-router-dom'
import { Slide } from 'react-awesome-reveal'

import { useMediaQuery, useTheme } from '@material-ui/core'
import Container from '@material-ui/core/Container'
import Paper from '@material-ui/core/Paper'

import Weather from './pages/Weather'
import InputPage from './pages/InputPage'

import './App.css'

const PrivateRoute = ({ children, condition, ...props }) => {
  return (
    <Route {...props}>
      {
        condition
          ? children
          : <Redirect to={{ pathname: '/' }} />
      }
    </Route>
  )
}

function App() {
  const [location, setLocation] = useState('')

  const theme = useTheme()
  const matchesXS = useMediaQuery(theme.breakpoints.down('xs'))

  return (
    <Router>
      <Container maxWidth={matchesXS ? 'xs' : 'sm'} style={matchesXS ? { padding: 0 } : {}}>
        <Slide direction="top" delay={500}>
          <Paper style={{ borderRadius: '10px', marginTop: matchesXS ? 0 : '2em' }} elevation={12}>
            <Switch>
              <Route exact path="/">
                <InputPage location={location} setLocation={setLocation} />
              </Route>
              <PrivateRoute path="/weather" condition={location}>
                <Weather location={location} />
              </PrivateRoute>
            </Switch>
          </Paper>
        </Slide>
      </Container>
    </Router>
  )
}

export default App
