/* eslint-disable react/jsx-filename-extension */

import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button'
import { Link } from 'react-router-dom'

const useStyles = makeStyles(() => ({
  inputContainer: {
    background: 'linear-gradient(190deg, rgba(10,129,187,1) 0%, rgba(0,197,139,1) 100%)',
    borderRadius: '10px',
    height: '50em',
  },
}))

const InputPage = ({ location, setLocation }) => {
  const classes = useStyles()
  return (
    <Grid container direction="column" className={classes.inputContainer} alignItems="center" justify="center">
      <Grid item>
        <TextField
          label="Location"
          variant="outlined"
          value={location}
          onChange={(e) => setLocation(e.target.value)}
        />
      </Grid>
      <Grid item>
        <Button component={Link} to="/weather">
          Submit
        </Button>
      </Grid>
    </Grid>
  )
}

export default InputPage
